<template>
<div id="post_create">
   <b-container fluid>
  <b-row class="my-1">
    <b-col sm="12">
      <b-form-input id="input-small" size="sm" v-model="postName.title" @keyup.enter="addPost" placeholder="Заголовок статьи"></b-form-input><br><br>
    </b-col>
  </b-row>
  </b-container>
   <b-form-textarea id="textarea-rows" placeholder="Текст статьи" rows="8" v-model="postName.text" @keyup.enter="addPost"></b-form-textarea><br>
   <b-button variant="success" v-on:click="addPost">Добавить</b-button>
</div>
</template>

<script>
import axios from "axios";


export default {
  name: "post_create",
  data() {
    return {
      posts: [],
      postName: {
        title: '',
        text: ''
      }
      };
  },


  async created() {
    try {
      const res = await axios.get('https://mycompanyblogs.pythonanywhere.com/api/blogs/',);
      this.posts = res.data;
    }catch (e) {
      console.error(e);
    }
  },

methods: {
  async addPost() {

      const res = await axios.post('https://mycompanyblogs.pythonanywhere.com/api/blogs/', { title: this.postName.title, text: this.postName.text});
      this.posts = [...this.posts, res.data];
      this.postName.title = "";
      this.postName.text = "";
      this.$router.push('/posts')
    }
  }

};
</script>
