<template>
<div id="post_detail_app">
   <h2>{{ post.title }}</h2><br>
   <h3>{{ post.text }}</h3>
   <router-link :to="{ name: 'allposts' }">Вернуться</router-link>
</div>
</template>

<script>
import axios from "axios";
import 'bootstrap'
export default {
  name: "post_detail_app",
  data() {
    return {
    post: []
      };
  },
  async created() {
    try {
      const res = await axios.get('https://mycompanyblogs.pythonanywhere.com/api/blogs/' + this.$route.params.id + '/',);
      this.post = res.data;
    }catch (e) {
      console.error(e);
    }
  }

};
</script>
