      this.posts = res.data;
    }catch (e) {
      console.error(e);
    }
  },

methods: {
 async UpdateBlog(data, id) {
    try {
     const res = await axios.put('https://mycompanyblogs.pythonanywhere.com/api/blogs/' + this.$route.params.id + '/', { title: this.update.title, text: this.update.text,  id: this.update.id,})
     this.post = res.data;
     this.id = id.data;

     }catch (e) {
       console.error(e);
       this.$router.push('/posts');
    }
  },
  }
};
</script>
