import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/views/Home'
import Posts from '@/views/blogs_pages/Posts'
import PostDetail from '@/views/blogs_pages/PostDetail'
import PostCreate from '@/views/blogs_pages/PostCreate'
import PostUpdate from '@/views/blogs_pages/PostUpdate'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
   { path: '', name: 'home', component: Home },
   { path: '/posts', name: 'allposts', component: Posts },
   { path: '/posts/:id', name: 'postdetail', component: PostDetail },
   { path: '/postcreate', name: 'postcreate', component: PostCreate },
   { path: '/postupdate', name: 'postupdate', component: PostUpdate },
  ]
})
